function wsocket() {

    let ws = null;
    let timer = null;
    let lekhCore = null;

    const ping = () => {
        if (ws != null) {
            try {
                ws.send('{}');
            } catch (e) { }
        }
    }

    const startTimer = () => {
        stopTimer();
        timer = setInterval(()=> {
            ping();
        }, 15000);
    }

    const stopTimer = () => {
        if (!!timer) {
            window.clearInterval(timer);
            timer = null;
        }
    }

    const connect = (docid, location) => {
        lekhCore = window.Module.avabodh_lekh_app_LekhCore_instance();
        ws = new window.WebSocket(location);
        
        ws.onopen = function(event) {
            console.log('connection opened: ', event);
            startTimer();
            lekhCore.coreIO().onWebSocketConnect();    
        };
    
        ws.onmessage = function(event) {
            lekhCore.coreIO().onWebSocketMessage(event.data);
        };
    
        ws.onclose = function(event) {
            console.log('connection closed:', event);
            stopTimer();
            lekhCore.coreIO().onWebSocketDisconnect();
        };
    
        ws.onerror = function(event) {
            console.log('connection error: ', event);
            stopTimer();
            lekhCore.coreIO().onWebSocketDisconnect();
        };
    }

    const disconnect = () => {
        if (ws != null) {
            try {
                ws.close();
            } catch (e) { }
        }
    }

    const send = (docid, data) => {
        if (ws != null) {
            if (ws.readyState != 1) {
                lekhCore.coreIO().onWebSocketDisconnect();
                return false;
            }
            try {
                ws.send(data);
            } catch(e) {
                lekhCore.coreIO().onWebSocketDisconnect();
                return false;
            }
            return true;
        }
    }

    return { connect, disconnect, send };
}

let wsmanager = wsocket();
export default wsmanager;